.card {
  padding: var(--card-padding-top)
    var(--card-pading-left-right)
    var(--card-pading-bottom);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: var(--card-width);

  border-radius: var(--card-border-radius);
  border: var(--card-border-width) solid var(--color-border);

  &__img {
    margin: 0 0 var(--card-img-margin-bottom);
    width: var(--card-img-width);
    height: var(--card-img-height);
  }

  &__title {
    margin: 0 0 var(--card-title-margin-bottom);
    padding: 0;

    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.5;
  }

  &__id {
    margin: 0 0 var(--card-id-margin-bottom);
    align-self: start;

    color: var(--color-secondary);
  }

  &__rating {
    margin: 0 0 var(--card-rating-margin-bottom);
    display: flex;
    align-self: stretch;
    justify-content: space-between;
    align-items: flex-end;

    color: var(--color-secondary);
  }

  &__price {
    margin: 0 0 var(--card-price-margin-bottom);
    display: flex;
    align-self: stretch;
    justify-content: space-between;
  }

  &__price-label {
    font-size: 1.2rem;
    line-height: 1.5;
    color: var(--color-secondary);
  }

  &__price-value {
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 1.12;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: var(--card-button-height);

    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.14;
    text-align: center;
    color: var(--color-white);
    background: var(--color-light-blue);

    text-transform: uppercase;
    text-decoration: none;

    border: var(--card-button-border-width) solid transparent;
    border-radius: var(--card-button-border-radius);

    &:hover {
      color: var(--color-light-blue);
      background-color: var(--color-white);
      border-color: var(--color-light-blue);
    }
  }
}
